import React from 'react';

import { NavigationContainer } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';

import LandingScreen from './components/auth/Landing';
import RegisterScreen from './components/auth/Register';
import LoginScreen from './components/auth/Login';
import Publications from './screens/Fil';
import FormFil from './screens/FormFil';
import Chat from './screens/Chat';

const Stack = createStackNavigator();

export default function App() {
  return (
    <NavigationContainer>
      <Stack.Navigator initialRouteName="Accueil">
        <Stack.Screen name="Accueil" component={LandingScreen} options={{headerShown: false}}/>
        <Stack.Screen name="Enregistrement" component={RegisterScreen}/>
        <Stack.Screen name="Connexion" component={LoginScreen}/>
        <Stack.Screen name="Publications" component={Publications}/>
        <Stack.Screen name="Publier" component={FormFil}/>
        <Stack.Screen name="Tchat" component={Chat}/>
      </Stack.Navigator>
    </NavigationContainer>
  );
}


