import React, { useState } from 'react';
import { StyleSheet, View, TextInput, Button } from 'react-native';
import { db } from '../config/firebase';
import { collection, addDoc } from 'firebase/firestore';

export default function FormFil({ navigation }) {  
  
  const [title, setTitle] = useState('');
  const [description, setDescription] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault()
      try {
        await addDoc(collection (db, 'temoignages'), {
          title: title,
          description: description       
        })
        alert('Ton post est publié 👏');
        navigation.navigate('Publications');

      } catch (err) {
        alert(err)
      }
  }

  return (
    <View style={styles.container}>    
      <TextInput value={title} onChangeText={(title) => {setTitle(title)}} placeholder="Titre" style={styles.input}></TextInput>
      <TextInput 
      multiline 
      numberOfLines={10} 
      value={description} 
      onChangeText={(description) => {setDescription(description)}} 
      placeholder="Post" 
      style={styles.input}>        
      </TextInput>
      <Button onPress={handleSubmit} title='Partager'/>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
    alignItems: 'center',
    justifyContent: 'center'    
  },

  input: {
    border: '2px solid #5840bb',
    padding: 5,    
    marginBottom: 5,
    width: 300  
  }
});
