import React from 'react';
import { View, Button } from 'react-native';

const firebaseConfig = {
    apiKey: "AIzaSyBU-YpTi7TTzCvygVP9UZpwV8S2CAsfo_I",
    authDomain: "jaiunepotedev.firebaseapp.com",
    projectId: "jaiunepotedev",
    storageBucket: "jaiunepotedev.appspot.com",
    messagingSenderId: "59986527208",
    appId: "1:59986527208:web:008e3eb8d5186d58a75a5d",
    measurementId: "G-BGY5KN3725"
  };

import { initializeApp } from "firebase/app";
initializeApp(firebaseConfig);

import { getAuth, signOut } from "firebase/auth";

const Signout = ({ navigation }) => {

    const handleSignOut = () => {
        const auth = getAuth();
        signOut(auth).then(() => {
        // Sign-out successful.
        alert('A bientôt !');
        navigation.navigate("Accueil");
        }).catch((error) => {
        // An error happened.
        console.log(error);
        });
    }

return (
    <View>       
        <Button 
            onPress={handleSignOut}
            title="Se déconnecter"
        />
    </View>
)

}

export default Signout