import React, { useState } from 'react';
import { View, Button, TextInput } from 'react-native';

const firebaseConfig = {
    apiKey: "AIzaSyBU-YpTi7TTzCvygVP9UZpwV8S2CAsfo_I",
    authDomain: "jaiunepotedev.firebaseapp.com",
    projectId: "jaiunepotedev",
    storageBucket: "jaiunepotedev.appspot.com",
    messagingSenderId: "59986527208",
    appId: "1:59986527208:web:008e3eb8d5186d58a75a5d",
    measurementId: "G-BGY5KN3725"
  };

import { getAuth, createUserWithEmailAndPassword } from "firebase/auth";
import { initializeApp } from "firebase/app";
initializeApp(firebaseConfig);

const Register = ({ navigation }) => {     
    
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');

    const handleSignUp = () => {
        const auth = getAuth();
        createUserWithEmailAndPassword(auth, email, password)
        .then(userCredential => {
            const user = userCredential.user;
            console.log(user.email);
            alert('Merci pour ton inscription 💛 Bienvenue et bon hack !');
            navigation.navigate("Accueil");
        })
        .catch((error) => {
            const errorCode = error.code;
            const errorMessage = error.message;
            console.log(errorCode, errorMessage);
        })
    }

        return (
            <View>
                <TextInput 
                    placeholder="Email"
                    value={email}
                    onChangeText={text => setEmail(text)}
                />

                <TextInput 
                    placeholder="Mot de passe"
                    secureTextEntry={true}
                    value={password}
                    onChangeText={password => setPassword(password)}
                />

                <Button 
                    onPress={handleSignUp}
                    title="S'enregistrer"
                />
            </View>
        )
    
}

export default Register