import React, { useState, useEffect} from 'react';
import { StyleSheet, Text, View, FlatList, ScrollView, Button } from 'react-native';
import { db } from '../config/firebase';
import { collection, getDocs } from 'firebase/firestore';

const Fil = ({ navigation }) => {
  const [posts, setPosts] = useState([]);

  useEffect(() => {
    getFil();
  });

  const getFil = () => {
    getDocs(collection(db, "temoignages")).then(snapshot => {       
        let myData = [];
        snapshot.forEach((doc) => {
          const fil = doc.data();
          myData.push({
            id: fil.id,
            title: fil.title,
            description: fil.description,
          });
        });
        setPosts(myData);
    })
      .catch((error) => {
        console.log("Error getting data: ", error);
      });
  };

  const Item = ({ title, description }) => (
    <View style={styles.center}>
      <View style={styles.card}>
        <Text style={styles.container}>
          {title}
        </Text>

        <Text style={styles.container}>
          {description}
        </Text>
      </View>
    </View>
  );

  const renderItem = ({ item }) => (
    <Item title={item.title} description={item.description} />
  );

  return (
    <ScrollView>      
      <FlatList      
        data={posts}
        renderItem={renderItem}
        keyExtractor={(item) => item.id}       
      />

      <Button style={styles.button} title='Partager une expérience' onPress={() => navigation.navigate('Publier')} />
    </ScrollView>    
  );   
};

const styles = StyleSheet.create({
  container: {    
    color: 'white',
    alignItems: 'center',
    justifyContent: 'center',
    fontFamily: "TitilliumWeb-Regular"              
  },
  card: {    
    width: 350, 
    borderRadius: 20,
    backgroundColor: '#5840bb',
    padding: 10             
  },
  center: {
    justifyContent: 'center',
    alignItems: 'center',
    marginTop: 10,
    marginBottom: 10
  },
  button: {
    position: 'absolute',                
    bottom: 0
  }
});

export default Fil;
